import React, { useEffect, useState } from "react";
import Link from "next/link";
import Axios from "axios";

export default () => {
  const [jobs, setJobs] = useState([]);

  useEffect(() => {
    getJobs().then(res => {
      console.log("jobsjobs", res);
      setJobs(res);
    });
  }, []);

  const getJobs = () => {
    return Axios.get(
      "https://api.lever.co/v0/postings/gojek?limit=10&mode=json"
    )
      .then(function(response) {
        // handle success
        return response.data;
      })
      .catch(function(error) {
        console.log(error);
      })
      .finally(function() {});
  };

  return (
    <React.Fragment>
      {jobs.length > 0
        ? jobs.map(job => {
            return (
              <div key={job.id}>
                {job.text}
                <Link
                  href="blog/[id]"
                  as={process.env.BACKEND_URL + `/blog/${job.id}`}
                >
                  <a>see more...</a>
                </Link>
              </div>
            );
          })
        : "Loading..."}
      
    </React.Fragment>
  );
};
